




















































import UploadImg from "@/components/uploads/upload.vue";
import { Component, Vue } from "vue-property-decorator";

import { apiUrl } from "../../api";
@Component({
    name: "system",
    components: {
        UploadImg
    }
})
export default class System extends Vue {
    private ruleForm = {
        id: "",
        sitename: "",
        record: "",
        code: "",
        logourl: "",
        adminname: ""
    };
    private rules: any = {
        sitename: [
            { required: true, message: "请输入站点名称", trigger: "blur" },
            {
                min: 2,
                max: 15,
                message: "长度在 2 到 15 个字符",
                trigger: "blur"
            }
        ],
        adminname: [
            { required: true, message: "请输入管理员名称", trigger: "blur" },
            {
                min: 2,
                max: 15,
                message: "长度在 2 到 15 个字符",
                trigger: "blur"
            }
        ]
    };

    created() {
        this.getInfo();
    }
    private getInfo() {
        (this as any).$axios.get(`${apiUrl.getSystem}`).then((res: any) => {
            if (res.data.code == 0) {
                this.ruleForm = res.data.message[0];
            }
        });
    }
    private submitForm(formName: string) {
        (this.$refs[formName] as any).validate((valid: boolean) => {
            if (valid) {
                (this as any).$axios
                    .post(`${apiUrl.addSystem}`, this.ruleForm)
                    .then((res: any) => {
                        console.log(res);
                        if (res.data.code == 0) {
                            this.$message.success("添加成功");
                        }
                    });
            } else {
                console.log("error submit!!");
                return false;
            }
        });
    }
    private resetForm(formName: string) {
        (this.$refs[formName] as any).resetFields();
    }
    changeUrl(img: string) {
        this.ruleForm.logourl = img;
    }
    removeUrl() {
        this.ruleForm.logourl = "";
    }
}
